import React, { useContext } from "react";
import { ResourceContext } from "../../components/Page";
import cs from "./History.module.css";
import { Link } from "@reach/router";

const History = () => {
  const [resource] = useContext(ResourceContext);
  const history = resource.history.read();
  return (
    <section className={cs.content}>
      <h1>All historical events</h1>
      <ul className={cs.events}>
        {history.map(event => (
          <li key={event.id}>
            <Link to={String(event.id)}>{event.title}</Link>
          </li>
        ))}
      </ul>
    </section>
  );
};

export default History;
